*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #333;
  font-family: 'Lato', sans-serif;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.mainWrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 15vw 1vw black;
}

@media (min-width: 960px) {
  .mainWrap {
    max-width: 960px;
    max-height: 700px;
  }
}

@media (min-width:1240px) and (orientation: landscape) {
  .mainWrap {
    max-height: 800px;
  }
}

@media (min-height:1240px) and (orientation: portrait) {
  .mainWrap {
    max-width: 960px;
    max-height: 1200px;
  }
}
header {
    background-color: #b61111;
    flex-basis: 15vh;
    display: flex;
    padding: 0 5vw;
    justify-content:space-around;
    align-items: center;
    border-bottom: 1px solid #940000;
    color: #fff;
}

header > span:first-child {
    font-size: 5.5vw;
}

header > span:last-child {
    font-size: 3.6vw;
}

@media (orientation: portrait) {
    header {
        flex-direction: column;
    }

    header > span:first-child {
        font-size: 10vw;
    }
    
    header > span:last-child {
        font-size: 6vw;
    }
}

@media (min-width: 960px) and (orientation: landscape) {
    header > span:first-child {
        font-size: 5vw;
    }
    
    header > span:last-child {
        font-size: 3vw;
    }
}

@media (min-height:1240px) and (orientation: portrait) {
    header > span:first-child {
        font-size: 7vw;
    }
    
    header > span:last-child {
        font-size: 4vw;
    }
}
.formWrap {
    flex-basis: 30vh;
    background-color: black;
    width: 100%; 
}

form {
    height: 30vh;
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.row {
    flex-basis: 30vw;
    display: flex;
    justify-content: space-around;
}

input[type="date"], select, .radioWrap {
    background-color: #222;
    color: #fff;
    border: 1px solid white;
    border-radius: 1vw;
    cursor: pointer;
}

input[type="date"] {
    padding: 1.2vw 3vw;
    font-family: Lato, sans-serif;
    text-align: right;
}

select {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    padding: 1.5vw 3vw;
}

.radioWrap {
    padding: 1.2vw 3vw;
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.radioWrap label {
    position: relative;
    font-size: 2.6vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

input[type="radio"] {
    margin-right: .5vw;
}

button {
    flex-basis: 30vw;
    padding: 1.5vw 4vw;
    font-family: 'Lato', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px;
    background-color: #222;
    color: #fff;
    border: 2px solid white;
    border-radius: 1vw;
    cursor: pointer;
    -webkit-transition: .3s;
    transition: .3s;
}

input[type="date"]:hover, select:hover, .radioWrap:hover, button:hover {
background-color: #111;
}

@media (orientation: portrait) {
    form {
        flex-direction: column;
        padding: 1.5vw;
    }
    
    .row {
        flex-basis: 8vw;       
    }
    
    input[type="date"], select, .radioWrap {
        border-radius: 2vw;
        width: 60vw;
        text-align: center;
    }

    input[type="date"] {
        text-align: left;
    }

    .radioWrap label {
        font-size: 4.4vw;
    }

    button {
        flex-basis: 8vw;
        border-radius: 2vw;
        width: 60vw;
    }
}

@media (min-width: 960px) and (orientation: landscape) {
    .row {
        flex-basis: 30vw;
    }
    
    input[type="date"] {
        padding: 1.8vw 3vw;
        font-size: 2.5vw;
    }
    
    select {
        padding: 2vw 3vw;
        font-size: 2.5vw;
    }
    
    .radioWrap {
        padding: 1.8vw 3vw;
        font-size: 2.5vw;
    }

    input[type="radio"] {
        width: 1.8vw;
        height: 1.8vw;
    }

    button {
        padding: 1.8vw 4vw;
        letter-spacing: 2px;
        font-size: 2.5vw;
    }
}

@media (orientation: portrait) and (min-height: 960px) {
    
    .row {
        flex-basis: 7vw;       
    }

    input[type="date"], select, .radioWrap {
        font-size: 3.4vw;
    }

    input[type="radio"] {
        width: 2.7vw;
        height: 2.7vw;
    }

    .radioWrap {
        padding: 1.5vw 1vw;
    }

    .radioWrap label {
        font-size: 3.4vw;
    }

    button {
        font-size: 4vw;
    }
}

@media (min-width:1240px) and (orientation: landscape) {
    .row {
        flex-basis: 30%;
    }
    
    input[type="date"] {
        padding: 1.2vw 1vw;
        font-size: 1.8vw;
    }
    
    select {
        padding: 1.5vw 1vw;
        font-size: 1.8vw;
    }
    
    .radioWrap {
        padding: 1.5vw 1vw;
    }

    .radioWrap label {
        font-size: 1.8vw;
    }
    
    input[type="radio"] {
        width: 1.6vw;
        height: 1.6vw;
    }

    button {
        padding: 1.5vw 4vw;
        letter-spacing: 2px;
        font-size: 2.2vw;
    }
}
.panelWrap {
    flex-basis: 45vh;
    background-color: #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3vw;
}

.panel {
    flex-basis: 80vw;
    margin: 3vw 5vw;
    text-align: center;
    background-color: rgba(0,0,0,.9);
    color: #fff;
    border-radius: 3vw;
    padding: 1vw;
}

.panel p {
    margin: .5vw;
    font-size: 2.6vw;
}

.date {
    font-weight: bold;
    letter-spacing: 1px;
}

.percent {
    font-weight: bold;
    font-size: 3.2vw;
}

.pos {
    color: green;
}

.neg {
    color: #b61111;
}

@media (orientation: portrait) {
    .panelWrap {
        font-size: 5vw;
    }
    
    .panel {
        padding: 2vw;
    }
    
    .panel p {
        margin: 1.4vw;
        font-size: 5vw;
    }
    
    span.newLine::after {
        content: '\A';
        white-space: pre;
    }

    .percent {
        font-size: 6vw;
    }
}

@media (orientation: landscape) and (min-width: 800px) {
    .panel {
        padding: 2vw;
    }
    
    .panel p {
        font-size: 1.7vw;
    }
    
    span.newLine::after {
        content: '\A';
        white-space: pre;
    }

    .percent {
        font-size: 2.2vw;
    }
}

@media (orientation: landscape) and (min-width: 960px) {
    .panel p {
        font-size: 2.2vw;
    }
    
    span.newLine::after {
        content: '\A';
        white-space: pre;
    }

    .percent {
        font-size: 3vw;
    }
}

@media (orientation: landscape) and (min-width: 1300px) {
    .panel {
        padding: .7vw;
    }
    
    .panel p {
        font-size: 2vw;
    }
    
    span.newLine::after {
        content: '\A';
        white-space: pre;
    }

    .percent {
        font-size: 2.4vw;
    }
}

@media (orientation: portrait) and (min-height: 1300px) {
    .panel {
        padding: 2vw;
    }
    
    .panel p {
        font-size: 3.2vw;
    }
    
    span.newLine::after {
        content: '\A';
        white-space: pre;
    }

    .percent {
        font-size: 4.5vw;
    }
}
footer {
    flex-basis: 10vh;
    line-height: 10vh;
    text-align: center;
    letter-spacing: 1px;
    background-color: #b61111;
    border-top: 1px solid #940000;
    font-size: 2.6vw;
}

footer a {
color: #fff;
}

@media (orientation: portrait) {
    footer {
        font-size: 4.4vw;
    }
}

@media (min-width: 960px) and (orientation: landscape) {
    footer {
        font-size: 2vw;
    }
}
